'use client';

import { useEffect } from 'react';
import { useRouter } from 'next/navigation';
import { Loader2 } from 'lucide-react';

/**
 * Redirect page: /admin/settings/checkout â†’ /admin/settings/checkout-enhanced
 *
 * This page has been merged into the enhanced checkout settings page.
 * All features including:
 * - Field Visibility
 * - Custom Messages
 * - Shipping Settings
 * - Regions & Cities
 * - Plus Phase 1-5 advanced features
 *
 * Are now available in the unified checkout settings page.
 */
export default function CheckoutSettingsRedirect() {
  const router = useRouter();

  useEffect(() => {
    // Redirect to enhanced checkout settings
    router.replace('/admin/settings/checkout-enhanced');
  }, [router]);

  return (
    <div className="flex items-center justify-center min-h-[400px]">
      <div className="text-center space-y-4">
        <Loader2 className="h-8 w-8 animate-spin text-gray-500 mx-auto" />
        <p className="text-muted-foreground">Redirecting to checkout settings...</p>
      </div>
    </div>
  );
}
